
<template>
  <div class="shop-sales">
    <!--  -->
    <MyBreadcrumb :list="breadcrumb" />
    <div class="shop-sales-container"></div>
  </div>
</template>

<script>
import MyBreadcrumb from "../../../components/MyBreadcrumb";

const echarts = require("echarts/lib/echarts");
require("echarts/lib/component/title");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
require("echarts/lib/chart/pie");

export default {
  components: { MyBreadcrumb },
  data() {
    return {
      breadcrumb: [
        { name: "", path: "/" },
        { name: "" },
        { name: "" },
      ],
      option: {
        title: {
          text: "",
          subtext: "",
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)",
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        legend: {
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 20,
          bottom: 20,
          data: ["T", "", "", "", ""],
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            data: [
              { name: "T", value: 41400 },
              { name: "", value: 5234 },
              { name: "", value: 15633 },
              { name: "", value: 9823 },
              { name: "", value: 2972 },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      },
    };
  },
  methods: {
    createCharts() {
      let myChart = echarts.init(
        document.querySelector(".shop-sales-container")
      );

      this.option && myChart.setOption(this.option);
    },
  },
  mounted() {
    this.createCharts();
  },
};
</script>

<style scoped>
.shop-sales-container {
  background: #fff;
  margin-top: 5px;
  padding: 5px;
  border-radius: 4px;
  height: 500px;
}
</style>