import users from '../json/users.json'
import cookie from 'js-cookie'
import NProgress from 'nprogress'

let uid = "0005"
let headPortrait = require("../../../static/img/headPortrait.jpg")

for (let k in users) {
    users[k].headPortrait = headPortrait
}


export default {

    // 
    login: option => {
        let loginForm = JSON.parse(option.body)
        // 
        if (users[loginForm.uid] && loginForm.password === '12345678a!') {

            return JSON.stringify({
                meta: {
                    msg: "",
                    status: 200
                },
                data: users[loginForm.uid]
            })
        } else {
            NProgress.done()
            return JSON.stringify({
                meta: {
                    status: 999,
                    msg: ''
                }
            })
        }
    },

    // 
    getInfo: () => {
        // token
        let token = cookie.get('token')
        NProgress.done()
        for (let k in users) {
            if (token === users[k].token) {
                return JSON.stringify({
                    meta: {
                        msg: "",
                        status: 200
                    },
                    data: users[k]
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 998,
                msg: ''
            }
        })
    },

    // 
    logout: option => {
        let uid = option.body
        console.log(uid + '')
        return JSON.stringify({
            meta: {
                status: 200,
                msg: ""
            }
        })
    },

    // 
    getUsers: () => {
        NProgress.done()
        let copyUsers = JSON.parse(JSON.stringify(users))
        let data = []
        for (let k in copyUsers) {
            delete copyUsers[k].token
            data.push(copyUsers[k])
        }

        return JSON.stringify({
            meta: {
                status: 200,
                msg: ""
            },
            data
        })
    },

    // 
    createUser: (option) => {
        let data = JSON.parse(option.body)
        data.uid = uid
        users[data.uid] = data
        uid = "000" + String(parseInt(uid) + 1)

        return JSON.stringify({
            meta: {
                status: 200,
                msg: ''
            }
        })
    },

    // 
    deleteUser: option => {
        let uid = option.body
        if (uid in users) {
            delete users[uid]
            return JSON.stringify({
                meta: {
                    status: 200,
                    msg: ''
                }
            })
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },

    // 
    changeUserInfo: option => {
        let data = JSON.parse(option.body)
        for (let i in users) {
            if (data.uid === users[i].uid) {
                users[i] = data
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },

    // 
    changeUserName: option => {
        let data = JSON.parse(option.body)
        for (let i in users) {
            if (data.uid === users[i].uid) {
                users[i].name = data.name
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },

    // 
    changeHeadPortrait: option => {
        let data = JSON.parse(option.body)
        for (let i in users) {
            if (data.uid === users[i].uid) {
                users[i].headPortrait = data.headPortrait
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },
}