<template>
  <div class="shop-sales">
    <!--  -->
    <MyBreadcrumb :list="breadcrumb" />
    <div class="salesVolume"></div>
  </div>
</template>

<script>
import MyBreadcrumb from "../../../components/MyBreadcrumb";
import * as echarts from "echarts";

export default {
  components: { MyBreadcrumb },
  data() {
    return {
      breadcrumb: [
        { name: "", path: "/" },
        { name: "" },
        { name: "" },
      ],
      option: {
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          data: ["T", "", "", "", ""],
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: ["", "", "", "", "", "", ""],
          },
        ],
        yAxis: [
          {
            type: "value",
          },
        ],
        series: [
          {
            name: "T",
            type: "line",
            stack: "",
            data: [120, 132, 101, 134, 90, 230, 210],
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [220, 182, 191, 234, 290, 330, 310],
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [150, 232, 201, 154, 190, 330, 410],
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [320, 332, 301, 334, 390, 330, 320],
          },
          {
            name: "",
            type: "line",
            stack: "",
            label: {
              normal: {
                show: true,
                position: "top",
              },
            },
            data: [820, 932, 901, 934, 1290, 1330, 1320],
          },
        ],
      },
    };
  },
  methods: {
    getSalesVolume() {
      let myChart = echarts.init(document.querySelector(".salesVolume"));
      this.option && myChart.setOption(this.option);
    },
  },
  mounted() {
    this.getSalesVolume();
  },
};
</script>

<style scoped>
.salesVolume {
  background: #fff;
  margin-top: 5px;
  padding: 5px;
  border-radius: 4px;
  height: 500px;
}
</style>