import Mock from 'mockjs'
import axios from 'axios'
import NProgress from 'nprogress'

// 
Mock.mock('http://text/api', 'post', require('../json/customers'))
let customers
axios({
    url: 'http://text/api',
    method: 'post',
}).then(res => {
    customers = res.data.data
})


export default {

    // 
    getCustomer() {
        NProgress.done()
        return JSON.stringify({
            meta: {
                status: 200,
                msg: ""
            },
            data: customers
        })
    },

    // 
    deleteCustomer: option => {
        let data = option.body
        for (let i in customers) {
            if (data === customers[i].uid) {
                customers.splice(i, 1)
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },

    // 
    changeCustomerInfo: option => {
        let data = JSON.parse(option.body)
        for (let i in customers) {
            if (data.uid === customers[i].uid) {
                customers[i] = data
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    },

    // 
    changeStatus: option => {
        let uid = option.body
        for (let i = 0; i < customers.length; i++) {
            if (uid === customers[i].uid) {
                customers[i].status = !customers[i].status
                return JSON.stringify({
                    meta: {
                        status: 200,
                        msg: ''
                    }
                })
            }
        }
        return JSON.stringify({
            meta: {
                status: 999,
                msg: ''
            }
        })
    }
}